
'use client';
import { costemAxios } from "@/utils";
import { useEffect, useEffectEvent, useState } from "react";
import { Territory } from "./components/Territory";

export default function Daily() {
  const [dailys, setDailys] = useState<any>();

  const getDailys = useEffectEvent(async () => {
    try {
      const response = await costemAxios.get<{ message: string; data: any[] }>('/loa/daily');
      if (response.status !== 200) { throw new Error(response.data.message); }
      
      setDailys(response.data);
    }
    catch (error) { console.error("loa/event api get error =>", error); }
  })
  
  useEffect(() => { getDailys(); }, [])

  console.log(dailys);
  return (<>
    <Territory data={dailys.territory} />



  </>);
}